.error {
    color: red;
    background:  rgb(74, 74, 74);
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}
.successful {
    color: rgb(108, 243, 108);
    background: rgb(74, 74, 74);
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}